@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap");
:root {
  --clr-paleBlue: #e0e8ff;
  --clr-brightBlue: #3829e0;
  --clr-veryPlateBlue: #f5f7ff;
  --clr-desaturatedBlue: #7280a7;
  --clr-darkBlue: #1f2f56;
  --clr-btn-hover: #766CF1;
  --clr-white: #fff;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  background-color: aliceblue;
  height: 100vh;
  display: grid;
  grid-template-rows: 1fr auto;
  place-items: center;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

ul {
  list-style-type: none;
}

a:link,
a:visited {
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

:root {
  --ff-primary: "Red Hat Display", sans-serif;
}

body {
  font-family: var(--ff-primary);
  font-size: 1.6rem;
  line-height: 1.6;
  color: var(--clr-desaturatedBlue);
}

h1, h2 {
  line-height: var(--line-height);
  color: var(--clr-darkBlue);
  font-weight: 900;
  text-transform: capitalize;
}

h1 {
  font-size: 2.2rem;
  margin-bottom: 1.7rem;
}

h2 {
  font-size: 1.4rem;
}

@media screen and (min-width: 375px) {
  h1 {
    font-size: 2.7rem;
  }

  h2 {
    font-size: 1.6rem;
  }
}
.card {
  background-color: var(--clr-white);
  display: grid;
  justify-items: center;
  text-align: center;
  border-radius: var(--border-radius);
}
.card__hero-img {
  border-radius: 2rem 2rem 0 0;
}
.card__info {
  padding: 4.8rem;
}
.card__text {
  max-width: 35.4rem;
  text-align: center;
  margin-bottom: 2rem;
}
.card__btn {
  margin-bottom: 32px;
}

.plan {
  padding: 2.5rem;
  text-align: left;
  background-color: var(--clr-veryPlateBlue);
  border-radius: var(--border-radius-btn);
  margin-bottom: 3.2rem;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  column-gap: 20px;
}
.plan__img {
  width: 4.8rem;
  height: 4.8rem;
}
.plan__info {
  display: grid;
}
.plan__change {
  color: var(--clr-brightBlue);
  font-weight: 700;
  text-decoration: underline;
  cursor: pointer;
}
.plan__change:hover {
  color: var(--clr-btn-hover);
  text-decoration: none;
}

@media screen and (max-width: 500px) {
  .card__info {
    padding: 2.4rem;
  }
}
.container {
  display: grid;
  place-items: center;
  height: 90rem;
  width: 100vw;
  max-width: 140rem;
  background: url("../images/pattern-background-desktop.svg"), var(--clr-paleBlue);
  background-repeat: no-repeat;
}

.btn {
  display: block;
  font-size: 1.5rem;
  font-weight: 900;
  line-height: var(--line-height);
  padding: 1.5rem 0;
  background-color: var(--clr-brightBlue);
  color: var(--clr-white);
  width: 100%;
  text-align: center;
  box-shadow: var(--box-shadow-btn);
  border-radius: var(--border-radius-btn);
  transition: var(--transition);
  cursor: pointer;
}

.btn:hover {
  background-color: var(--clr-btn-hover);
}

.btn-small {
  font-family: var(--ff-primary);
  border: none;
  background-color: transparent;
  text-transform: capitalize;
  font-size: 1.5rem;
  font-weight: 900;
  line-height: var(--line-height);
  color: var(--clr-desaturatedBlue);
  transition: var(--transition);
  cursor: pointer;
}

.btn-small:hover {
  color: var(--clr-darkBlue);
}

:root {
  --border-radius: 20px;
  --border-radius-btn: 11px;
  --line-height: 1.3;
  --box-shadow-btn: 0px 20px 20px rgba(56, 42, 225, 0.190291);
  --bow-shadow-card: 0px 40px 40px -20px rgba(13, 48, 189, 0.151826);
  --transition: all 0.2s linear;
}